import React, { useEffect } from "react";

import Navbar from "../../components/genral_components/Navbar/Navbar";

import MainCategoriesBanner from "../../templates/maincategories/maincategoriesbanner/MainCategoriesBanner";

import SocialMediaMarketingMainDescription from "../../components/social_media_marketing_page_components/main_description/SocialMediaMarketingMainDescription";

import MainCategoriesCards from "../../templates/maincategories/maincategoriescards/MainCategoriesCards";

import SecondaryDescription from "../../components/social_media_marketing_page_components/secondary_description/SecondaryDescription";

import Carousel from "./../../components/home_page_components/carousel/Carousel";

import Pricing from "./../../components/services_page_components/pricing/Pricing";

import Skills from "./../../components/home_page_components/ourskills/Skills";

import OurWork from "./../../components/home_page_components/ourwork/Ourwork";

import Footer from "./../../components/genral_components/Footer/Footer";

import backgroundImage from "./background-image.jpg"
import imageOne from "./ImageOne.png"
import imageTwo from "./ImageTwo.png"

import SocialMediaAdvertisingPurple from "./SocialMediaAdvertisingPurple.png"
import SocialMediaAdvertisingWhite from "./SocialMediaAdvertisingWhite.png"

import SocialMediaBrandingPurple from "./SocialMediaBrandingPurple.png"
import SocialMediaBrandingWhite from "./SocialMediaBrandingWhite.png"

import SocialMediaManagementPurple from "./SocialMediaManagementPurple.png"
import SocialMediaManagementWhite from "./SocialMediaManagementWhite.png"

import { useLocation } from "react-router-dom";

export default function SocialMediaMarketingPage() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="social-media-marketing-page-main-container">
      <Navbar />

      <MainCategoriesBanner
        heading="Social Media Marketing Agency"
        paragraph="Ignite your brand's social media presence and unleash its full potential with our expert social media marketing services"
        button1="Let's Chat"
        button2="Explore our projects"
        bannerBackgroundImage={backgroundImage}
      />

      <SocialMediaMarketingMainDescription />

      <SecondaryDescription />

      <MainCategoriesCards
        numberOfCards={3}
        card1heading="Social media advertising"
        card1paragraph="Interac8 as a Social media marketing agency offers a variety of ad types to accommodate diverse marketing objectives. These can include sponsored content, story ads, carousel ads, slideshow ads, image ads, video ads, and more. The target market, campaign goal, and accessible creative assets are some examples of variables that influence the choice of ad format"
        card1Link="/services/socialmediamarketing/socialmediaadvertising"
        cardOneImageWhite={SocialMediaAdvertisingWhite}
        cardOneImagePurple={SocialMediaAdvertisingPurple}

        card2heading="Social Media Branding"
        card2paragraph="Social media is an increasingly popular medium for brand building. With billions of active users on platforms such as Instagram and Twitter, businesses have increasingly utilized these channels to connect with their customer base around the world."
        card2Link="/services/socialmediamarketing/socialmediabranding"
        cardTwoImageWhite={SocialMediaBrandingWhite}
        cardTwoImagePurple={SocialMediaBrandingPurple}

        card3heading="Social Media Management"
        card3paragraph="Interac8, as a social media marketing agency, creates material that is appropriate for each social media site, including text, photographs, videos, and infographics. To successfully interac8 with and reach the target audience, entails managing an organization's presence on websites and apps like Facebook, Twitter, Instagram, LinkedIn, and others."
        card3Link="/services/socialmediamarketing/socialmediamanagement"
        cardThreeImageWhite={SocialMediaManagementWhite}
        cardThreeImagePurple={SocialMediaManagementPurple}

        pic1={imageOne}
        pic2={imageTwo}
      />

      {/* <SecondaryDescription /> */}

      <Carousel
        nameOne="Lily Anderson"
        reviewOne="We were genuinely impressed! The social media marketing job done by Interac8 was fantastic and on time with all of the deliverables. The marketing provided straightforward and reliable outcomes. Everything was flawlessly planned out and delivered in a very systematic manner."
        nameTwo="Ryan Smith"
        reviewTwo="I was having trouble expanding my business, and all I needed was effective marketing. Interac8 assisted me in giving my company an advantage over rivals by using high-quality material and stunning visuals!"
        nameThree="Gavin Clark"
        reviewThree="After looking around and trying out one or two agencies to handle our social media, someone recommended interac8 to us. The experience and results so far have been outstanding!"
      />

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />

      <OurWork />

      <Footer />
    </div>
  );
}
